#myPage {
  .title_wrap {
    display: flex; justify-content: space-between; align-items: center;
    .title {font-size: 24px; font-weight: bold;}
    .user_img {
      position: relative; width: 50px; height: 50px; border-radius: 50%; overflow: hidden;
      img {@include posCenter; width: 100%;}
    }
  }
  .user_info {
    margin: 30px 0;
    dl{
      &:not(:first-child) {
        margin-top: 20px;
      }
      dd {
        font-size: 16px; color: rgba(0, 0, 0, .4);
      }
      dt {
        font-size: 18px; margin-top: 8px;
      }
    }
  }
  .logout_btn {
    width: 100%; height: 30px; line-height: 24px; color: #fff; text-align: center;  border-radius: 5px; background: #83c4be;
  }
}