#userProfile {
  position: relative;
  .inner {
    padding: 50px 10px 0;
    .top {
      display: flex; align-items: center;
      .user_img {
        position: relative; width: 80px; height: 80px; border-radius: 50%; overflow: hidden;
        img {
          @include posCenter; width: 100%;
        }
      }
      .user_info {
        margin-left: 10px; 
        .user_name {
          margin-bottom: 20px; font-size: 20px; font-weight: bold;
          .small {
            font-size: 16px; font-weight: 400;
          }
        }
        .user_dept {
          font-size: 16px; color: rgba(0, 0, 0, 0.4);
        }
      }
    }
    .btm {
      margin-top: 40px; padding-left: 10px;
      .list {
        padding: 16px 0; border-top: 1px solid rgba(0, 0, 0, .1);
        .icon {
          display: block; margin-bottom: 6px; font-size: 20px;
          &.enter_date {
            font-size: 16px; color: rgba(0, 0, 0, 0.4);
          }
          .fa-phone {
            color: $default_color;
          }
          .fa-at {
            color: rgba(0, 0, 0, .4);
          }
        }
        &:first-child {
          padding-top: 0; border-top: 0;
        }
      }
    }
  }
  .btn_back {
    position: absolute; top:0; left: 5px; font-size: 20px;
  }
}