@import "./variable";
@import "./mixin";
@import "./reset";
@import "./main.scss";
@import "./book.scss";
@import "./contact.scss";
@import "./myPage.scss";
@import "./profile.scss";

body {font-family: "NotoSans-Regular", "맑은 고딕", "Malgun Gothic", sans-serif; font-weight: 400; font-size: 18px; color: #000;}
a{text-decoration: none; color: #333;}
#app {
  padding: 10px 20px 100px;
}

#loginLogo {
  text-align: center; margin-bottom: 30px;
}

#loginPage {
  padding: 0 30px; margin-top: 60px;
  
  .input_item {
    position: relative; margin-top: 34px;
    input {
      padding-left: 10px; margin-top: 10px; height: 40px; border: 1px solid #eee; border-radius: 2px;
      
      &:focus {
         outline: none; border: 1px solid $default_color;
      }
      &::placeholder {
        font-size: small; color: rgba($color: #000000, $alpha: .5);
      }
    }
    label {
      font-size: 16px; color: #666;
    }
    &.error {
      border: 1px solid red;
    }

    &.focus {
      border: 1px solid $default_color;
    }
  }
  .login_btn {
    margin-top: 30px; padding: 15px 0; width: 100%; font-size: 16px; font-family: "NotoSans-Medium", "맑은 고딕", "Malgun Gothic", sans-serif; border-radius: 4px; color: #fff; background-color: $default_color;
  }
  .error_msg {
    display: flex; align-items: center; width: 100%; min-height: 50px; color: red; font-size: 12px;
  }
  .valid_check {
    position: absolute; bottom: -25px; left: 0; margin-top: 8px; color: red; font-size: 12px;
  }
}

#header {
  .wrap {
    display: flex; justify-content: space-between; align-items: center; margin-bottom: 20px;
    #mainLogo {
      width: 80px;
      img {width: 100%;}
    }
    .weather_info {
      align-self: flex-end;
      .weather_swipe {
        height: 45px;
        .swiper-wrapper {
          align-items: flex-end;
          .swiper-slide {
            background: #fff;
            &.temp_info {
              .weather_stat {
                display: flex; align-items: center; justify-content: flex-end;
                .img {
                  width: 45px; height: 45px;
                  img {
                    width: 100%;
                  }
                }
                .curr_temp {
                  margin-right: 10px; font-size: 14px;
                  .bold {
                    font-weight: bold; margin-right: 5px;
                  }
                }
                .temp_min_max {
                  font-size: 14px;
                  .min {
                    color: #2586ee;
                    &:after {
                      height: 100%;
                      padding: 0 4px;
                      color: #000;
                      content: "/";
                    }
                  }
                  .max {
                    color: #e9564a;
                  }
                }
              }
            }    
            &.air_info {
              .air_stat {
                display: flex; align-items: center;justify-content: flex-end; height: 100%;
                .txt {
                  font-size: 14px; color: rgba(0, 0, 0, .4);
                  .bold {
                     color: rgba(0, 0, 0, 1); font-weight: bold;
                  }
                } 
                .air {
                  display: flex; align-items: center;
                  .img {
                    margin-left: 5px; width: 20px; height: 20px;
                    &.good {
                      background: url("~assets/images/icon_air_good.png") no-repeat center / cover;
                    }
                    &.neutral {
                      background: url("~assets/images/icon_air_neutral.png") no-repeat center / cover;
                    }
                    &.bad {
                      background: url("~assets/images/icon_air_bad.png") no-repeat center / cover;
                    }
                    &.worst {
                      background: url("~assets/images/icon_air_worst.png") no-repeat center / cover;
                    }
                  }
                  &.air_2_5 {
                    margin-right: 5px;
                  }
                }

              }
            }        
          }
        }
      }
    }
  }
}

#navBar {
  position: fixed; bottom: 0; left: 0; z-index: 10; display: flex; width: 100%; border-top: 1px solid #eee; background: #fff;

  a{
    display: flex; flex-wrap: wrap; align-items: center; justify-content: center; padding: 8px 0; width: 25%;

    &.router-link-active {
      color: $default_color;
      .cate_name {
        display: block; width: 100%;
      }
    }
    .icon {
      display: block; text-align: center;
      svg {
        width: auto; height: 20px;
      }
    }
    .cate_name {
      display: none; padding-top: 0; text-align: center; font-size: 14px;
    }
  }
}

.title_comp {
  position: relative; margin: 40px 0 20px; padding: 0 0 0 12px; font-size: 22px;  color: #000;  font-weight: bold; line-height: 24px;
  &:after {
    content: ""; display: block; position: absolute; top: 0; left: 0; width: 6px;  height: 24px;  border-radius: 2px; background: $default_color;
  }
}


#notFound {
  padding: 20% 0; text-align: center;
  .warning {
    font-size: 30vw; color: $default_color;
  }
  .big_txt {
    margin: 20px 0 10px; font-size: 60px; font-weight: bold; color: #000;
  }
  .txt1 {
    color: #000;
  }
  .btn_link {
    display: inline-block; margin-top: 26px; padding: 3px 6px; border: 1px solid $default_color; color: $default_color; font-size: 14px;
  }
}

.modal-mask {
  position: fixed; z-index: 9998; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.5); display: table; transition: opacity 0.3s ease;
  .modal-wrapper {
    display: table-cell; vertical-align: middle;
    .modal-container {
      position: relative; margin: 0px auto; padding: 20px 30px; width: 85%; max-height: 85vh; overflow-y: auto; background-color: #fff; border-radius: 2px; box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33); /* transition: all 0.3s ease; */ font-family: Helvetica, Arial, sans-serif;
      .modal-header {
        display: flex; justify-content: space-between;
        .title {
          margin-top: 0; color: #42b983;
        }
      }
      .modal-body {
        margin: 20px 0;
      }
      .modal-footer {
        .footer{
          .modal-default-button {
            float: right;
          }
        }
      }

      .btn_close{
        position: absolute; top: 10px; right: 10px; width: 18px; height: 18px;
        svg {
          width: 100%;
        }
      }
    }
  }
/*
