#contactForm {
  .list {
     position: relative; padding: 15px; border-bottom: 1px solid rgb(0, 0, 0, .1);
    a {
      display: flex; align-items: center;
      .user_img {
        position: relative; width: 50px; height: 50px;
        img {
          @include posCenter; width: 100%; height: 100%; border-radius: 50%;
        }   
      }
      .info_wrap {
        margin-left: 30px; display: flex; align-items: center;
        .name {
          font-weight: 700;
        }
        .info {
          margin-left: 20px; font-size: 12px; color: #333;
        }
      }
      .icon_wrap {
        position: absolute; right: 15px; color: $default_color;
      }
    }
  }
}