//style
@mixin abbr($w:100%) {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: $w;
}

@mixin borderRadius($a:50%, $b:50%, $c:50%, $d:50%) {
  -webkit-border-radius: $a $b $c $d;
  -ms-border-radius: $a $b $c $d;
  -o-border-radius: $a $b $c $d;
  border-radius: $a $b $c $d;
}

@mixin clear {
  &:after {
    content: "";
    display: block;
    clear: both;
  }
  
}
@mixin posCenter() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

