@mixin multiAbbr($line:2, $w:100%, $h:100%) {
  // multiAbbr는 IE에서 사용금지
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  width: $w;
  height: $h;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $line;
}

@mixin positionL($x, $y) {
  position: absolute;
  left: $x;
  top: $y;
    
  @if $x == 50% and $y == 50% {
    transform: translate(-50%, -50%);
  }
  @else if $x == 50% {
    transform: translateX(-50%);
  }
  @else if $y == 50% {
    transform: translateY(-50%);
  }
}

@mixin positionR($x, $y) {
  position: absolute;
  right: $x;
  top: $y;
    
  @if $x == 50% and $y == 50% {
    transform: translate(-50%, -50%);
  }
  @else if $x == 50% {
    transform: translateX(-50%);
  }
  @else if $y == 50% {
    transform: translateY(-50%);
  }
}

@mixin textTransUp() {
  text-transform: uppercase;
}



//mediaquery
@mixin mobile() {
  @media all and (min-width:#{$_mobile}) and (max-width:#{$_tablet - 1px}) {
    @content;
  }
}

@mixin tablet() {
  @media all and (min-width:#{$_tablet}) and (max-width:#{$_desktop - 1px}) {
    @content;
  }
}

@mixin desktop() {
  @media all and (min-width:#{$_desktop}) {
    @content;
  }
}
//<< common mixin end >>


@function calc-percent($target, $container) {
  @return ($target / $container) * 100%;
}

// 폰트 설정
@mixin font-face($name, $weight, $style: normal) {
  @font-face {
    font-family: "#{$name}"; 
    src: url(~assets/fonts/#{$name}.eot);
    src: url(~assets/fonts/#{$name}.eot?#iefix) format("embedded-opentype"), url(~assets/fonts/#{$name}.woff) format("woff"), url(~assets/fonts/#{$name}.ttf) format("truetype");
    font-weight: $weight;
    font-style: $style;
  }
}
@include font-face("NotoSans-Black", 800);
@include font-face("NotoSans-Bold", 700);
@include font-face("NotoSans-Medium", 500);
@include font-face("NotoSans-Regular", 400);
@include font-face("NotoSans-Light", 300);
@include font-face("NotoSans-Thin", 100);
