
#bookPage {
	.tab_btn {
		display: flex; margin-bottom: 40px; width: 100%;
		a {
			display: block; padding: 12px 0; width: 50%; text-align: center; border-bottom: 2px solid #eee;
      &.router-link-active {
        font-weight: bold; border-bottom: 3px solid $default_color;
      }
		}
	}
  .search_area {
    display: flex; align-items: center; width: 100%;
    .txt {
      width: 35%; color: $default_color;
    }
    .input_area {
      position: relative; width: 65%;
      input {
        padding-left: 10px; width: 100%; height: 40px; border: 1px solid #eee; border-radius: 2px;

        &:focus {
          outline: none; border: 1px solid $default_color;
       }

        &::placeholder {
          font-size: small; color: rgba($color: #000000, $alpha: .5);
        }
      }
      button {
        position: absolute; top: 6px; right: 0; width: 30px; height: 30px;
      }
    }
  }

  .new_book_swipe {
    padding: 40px 0 0;
  }

  .swiper-container {
    .swiper-slide {
      position: relative;
    }
    .img_area {
      position: relative; padding-top: 66.25%; overflow: hidden;
      img {
        @include posCenter; width: 100%;
      }
    }
    .book_info {
      position: absolute; bottom: 20px; left: 50%; transform: translateX(-50%); padding: 24px; width: 80%; text-align: center; border: 1px solid #eee; border-radius: 10px; box-shadow: 0px 7px 18px 1px rgb(0 0 0 / 20%); background: #fff;
      .book_name {
        font-size: 16px;
      }
      .book_auth {
        font-size: 13px;
      }
    }
  }
}

#bookPage {
  ul.category_list {
    display: flex; flex-wrap: wrap;
    li {
      position: relative; padding-top: 56.25%; margin: 0 2% 10px; width: 45%; background: teal;
      .title{
        position: absolute; top: 20px; left: 20px; z-index: 1; font-size: 14px; color: #fff;
      }

      @for $i from 1 through 18{
        &:nth-child(#{$i}){
          background: url("~assets/images/book-category-cover#{$i}.jpg") no-repeat center center / cover;
        }
      }
      &:after{
        content: ""; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: rgba(0,0,0,0.4);
      }
    }
  }
  .book_list {
    display: flex; padding: 20px 0; border-top: 1px solid #ccc;
    .img_info {
      position: relative; width: 30%;
      img {
        width: 100%;
      }
    }
    .book_info {
      padding-left: 20px; width: 70%; font-size: 14px;
      .name {
        margin-bottom: 20px; font-size: 18px; font-weight: bold;
    }
      .publisher {
        margin: 2px 0;
      }
    }
  }
  .btn_more_area {
    text-align: center;
    .btn_more {
      padding: 8px 12px; color: #fff; background: $default_color;
    }
  }
}

#readingKing {
  .list {
    position: relative; padding: 15px 40px; border-bottom: 1px solid rgb(0, 0, 0, .1);
    .list_box {
       display: flex; align-items: center;
      .list_index {
        @include positionL(0, 50%); text-align: center;
      }
      .user_img {
        position: relative; width: 50px; height: 50px;
        img {
          @include posCenter; width: 100%; height: 100%; border-radius: 50%;
        }
        .icon_wrap { 
          @include positionL(50%, -20px); color: #ffdf65;
        }        
      }
      .box {
        text-align: center; width: calc(100% - 50px);
        .pos {
           font-size: 12px; color: rgb(0, 0, 0, .4)
        }
      }
      .rent_count {
        @include positionR(0, 50%); display: flex; align-items: center; justify-content: center; padding: 5px 2px; min-width: 50px; text-align: center; background-color: rgb(0, 0, 0, .1); border-radius: 4px;
        .icon_wrap {
          margin-right: 4px; font-size: 12px; color: #f89b00;
        }
      }
    }
  }
}
