.ezstory_swipe {
  a {
    position: relative; display: block;

    .ezstory_img {
      position: relative; padding-top: 56.25%; overflow: hidden; width: 100%;

      img {
        @include posCenter(); width: 100%; height: auto;
      }

      &:after {
        content: ""; position: absolute; bottom: 0; left: 0; width: 100%; height: 70%; background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%);
      }
    }
    .ezstory_tit {
      position: absolute; bottom: 20px; left: 20px; width: 90%; font-size: 18px; color: #fff; font-weight: bold; word-break: keep-all;
    }
  }
}
#ezstory {
  margin-top: 40px;
  .swiper-pagination {
    position: relative; display: flex; justify-content: center; margin: 20px 0 0 0; width: calc(100% - 8px);
  }
  .swiper-pagination-bullet {
    margin: 0 4px; border-radius: 8px; transition: all 0.3s ease;
  }
  .swiper-pagination-bullet-active {
    width: 30px; background: $default_color;  
  }
}

#anniverse {
  .tab_area {
    display: flex; justify-content: space-between; margin-bottom: 10px;
    .tab_wrap{
      display: flex;
      .tab {
        color: rgba(0, 0, 0, .4); font-size: 16px; font-weight: bold;
        &:not(:first-child) {margin-left: 10px;}
        &.active {color: rgba(0, 0, 0, 1);}
      }
    }
    .btn {font-size: 14px;}
  }
  .container {
    .tab_con {
      .swiper-container {
        .swiper-wrapper {
          .swiper-slide {
            width: 60px;
            .user_img {
              position: relative; padding-top: 100%; width: 100%; border-radius: 8px; overflow: hidden;
              img {
                @include posCenter; width: 100%; 
              }
            }
            .user_name {
              margin: 10px 0 5px; text-align: center; font-size: 16px;
            }
            .work_year {
              margin: 0 auto; width: 20px; height: 18px; border-radius: 5px; color: #fff; text-align: center; font-size: 12px; font-weight: 300; background: $default_color;
            }
            .icon_birthday {
              height: 18px; font-size: 14px; text-align: center; color:$default_color;
            }
          }
        }
      }
    }
  }
  .modal-container {
    .modal-header {
      justify-content: center;
    } 
    .list_area {
      .list_wrap {
        &:not(:first-child) {
          margin-top: 60px;
        }
        .sub_title {
          font-size: 14px; font-weight: bold;
        }
        .list {
          padding: 20px 0; border-bottom: 1px solid rgba(0, 0, 0, .1);
          .date {
            font-size: 15px; color: rgba(0, 0, 0, 0.4);
          }
          .user_wrap {
            display: flex; flex-wrap: wrap;
            .user {
              display: flex; align-items: center; padding: 20px 20px 0 0;
              .img {
                position: relative; width: 40px; height: 40px; border-radius: 8px; overflow: hidden;
                img {@include posCenter; width: 100%;}
              }
              .user_info {
                margin-left: 16px;
                .name {
                  margin-bottom: 20px; font-size: 16px; font-weight: bold;
                }
                .call_name {
                  font-size: 14px; color: rgba(0, 0, 0, .4);
                }
              }
            }
          }
        }
        &:last-child .list {
          &:last-child {
          border-bottom: 0px;
          }
        }
      }
    }
  }
}

#boardList {
  margin-top: 40px;

  .board_tab {
    .swiper-slide {
      padding: 0 0 20px 0; width: auto; cursor: pointer; 

      a {
        display: block; color: #888; font-weight: normal;
      }
      
      &-select {
        font-weight: bold;
        a {
          color: #000;
        }
      }
    }
  }
  .list {
    display: flex; /* align-items: center; */ padding: 20px 0; border-bottom: 1px solid rgba(0, 0, 0, .1);
    &:nth-child(1) {padding-top: 0;}

    .user_info {
      .user_img {
        position: relative; width: 50px; height: 50px; overflow: hidden; @include borderRadius();
  
        img{
          @include posCenter(); width: 100%;
        }
      }
      .user_name {margin-top: 10px;}
    }

    .text_info {
      display: flex; flex-direction: column; justify-content: space-between; padding: 0 40px 0 20px;
      .title {
        @include multiAbbr(2, 100%, auto);
        font-size: 16px; color: #000; font-weight: bold;
      }
      .e_area {
        display: flex; align-items: center;
        .recmd {
          // width: 40px;
          display: flex; align-items: center; margin-right: 10px;
          .fa-heart {font-size: 12px; margin-right: 4px; color: red;}
        }
        .read {
          // width: 50px;
          display: flex; align-items: center; margin-right: 10px;
          .fa-eye {font-size: 12px; margin-right: 4px;}
        }
        .date {
          font-size: 12px; font-weight: 300;
        }
      }
    }
  }
}

#meetingRoom {
  margin-top: 40px;
  .room_condition {
    display: flex; justify-content: space-between; padding: 0 5%;
   
    .condition {
      text-align: center;
      .txt {
        display: block;
      }
      .num {
        font-size: 20px;
      }
    }
  }
  .room_list {
    margin-top: 14px;
    .list {
      display: flex; align-items: center; padding: 12px 0; border-bottom: 1px solid #eee;
      .room_name {
        width: 25%; text-align: center; font-size: 16px; font-weight: bold;        
      }
      .progress_bar {
        width: 50%; text-align: center; font-size: 13px;
        .bar_box {
          position: relative; margin: 5px 0; width: 100%; height: 5px; border: 1px solid $default_color; border-radius: 5px; overflow: hidden;
          .bar {
            position: absolute; top: 0; left: 0; width: 20%; height: 100%; background: $default_color;
          }
        }
        .bar_box.completed {
          .bar {width: 100%;}
        }
        .bar_box.notYet {
          .bar {width: 0%;}
        }
      }
      .user {
        width: 25%;
        .img_area {
          margin: auto; width: 30px; height: 30px; @include borderRadius(); overflow: hidden;
          img {
            width: 100%;
          }
        }
        .name {
          padding-top: 5px; text-align: center; font-size: 12px;
        }
      }
    }
  }
}

#meetingSchedule {
  margin-top: 40px;
  .users {
    position: relative; display: flex; margin-top: 18px;
    .user {
      position: relative; margin-right: 10px; 
      .user_img {
        width: 30px; height: 30px; @include borderRadius(); overflow: hidden;
        img {
          width: 100%;
        }
      }

      .name {
        padding-top: 8px; text-align: center; font-size: 12px;
      }
    }

    .time{
      position: absolute; top: 0; right: 0; padding: 2px 6px; color: #fff; font-size: 12px; background: orange; @include borderRadius(5px, 5px, 5px, 5px);
    }
  }
  .loc_box + .loc_box {
    padding-top: 20px;
  }
}

#newMember {
  margin-top: 40px;
  .swiper-container {
    padding-right: 5px;
    // overflow: unset;
    .swiper-wrapper {
      padding: 5px 2px 5px 2px;
      .swiper-slide {
        width: 90%;
        .card-w {
          position: relative; padding: 50px 20px 24px 20px; border-radius: 2px; box-shadow: 0 1px 4px rgb(0, 0, 0,.2);
          p {
            margin: 0;
          }
          .easy-logo {
            position: absolute; top: 30px; right: 30px; width: 70px;
            img {
              width: 100%;
            }
          }
          .new-name {
            font-size: 24px; font-weight: 700; letter-spacing: -.08em
          }
          .dept-info{
            margin-top: 20px; font-weight: 500;
          }
          .user-info{
            margin-top: 20px; font-weight: 500; font-size: 11px; letter-spacing: -.06em;
          }
          .easy-data{
            font-size: 11px; font-weight: 500; letter-spacing: -.03em;
          }
          .easy-data p {
            margin-top: 8px;
          }
          .easy-info {
            margin-top: 8px;
          }
          .easy-info p {
            margin-top: 0;
          }
        }
      }
    }
  }
}
